.switcher {
  align-items: center;
  display: flex;
}

.switcher__input {
  display: none;
}

.switcher__label {
  cursor: pointer;
  position: relative;
}

.switcher__label:before {
  content: "";
  width: 40px;
  height: 20px;
  transition: border-color 250ms linear background-color 250ms linear;
  border: 1px solid #4b48db;
  border-radius: 50px;
  display: block;
}

.switcher__label:after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #4b48db;
  border-radius: 50%;
  transition: transform .25s linear, background-color .25s linear;
  display: block;
  position: absolute;
  top: 2px;
  left: 2px;
}

.switcher__input:checked + .switcher__label:after {
  background-color: #fff;
  transform: translateX(20px);
}

.switcher__input:checked + .switcher__label:before {
  background-color: #4b48db;
  border-color: #fff;
}

.switcher__input input {
  display: none;
}

.switcher__slider {
  cursor: pointer;
  background-color: #fff;
  border-radius: 50px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switcher__slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #4b48db;
  border-radius: 100px;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 2px;
  right: 2px;
}

input:checked + .switcher__slider {
  background-color: #4b48db;
  border-color: #fff;
}

input:checked + .switcher__slider:before {
  background-color: #fff;
}

input:focus + .switcher__slider {
  box-shadow: 0 0 1px #4b48db;
}

input:checked + .switcher__slider:before {
  transform: translateX(20px);
}

